@import url(https://fonts.googleapis.com/css2?family=Sarabun:ital,wght@1,200&display=swap);
:root{
    --font-family:'Sarabun', sans-serif;
}

*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
body{
   overflow-x: hidden;
}

h2{
   font-family: 'Sarabun', sans-serif;
   font-family: var(--font-family);
}
p{
   font-family: 'Sarabun', sans-serif;
   font-family: var(--font-family);

}


.card{
    width: 80%;
    height: 50vw;
    display: flex;
    margin: 10px auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(21, 255, 255);
    border-radius: 20px;
    box-shadow: 0 4px 25px -9px black;

}

.card>a{
    display: flex;
    justify-content: center;
}
.card>a>img{
    width: 80%;
    border-radius:10px ;
    box-shadow: 0 4px 25px -2px black;
}
.card>a>img:hover{
    width: 70%;
}
.card>h2{
    font-size: 4vw;
    padding: 4%;
   
}

@media screen and (min-width:500px) {
    
    .card>a>img{
        margin: 9px 0;
        width: 81%;
        height: 27vw;
        object-fit: cover;    
    } 
        
}
@media screen and (min-width:750px) {
    .card{
        height: 45vw;
    }
    .card:hover{
        width: 85%;
        height: 48vw;
    }
    .card>h2{
        font-size: 2.8vw;
        padding: 4%;
    }
    .card>a>img{
        margin: 9px 0;
        width: 81%;
        height: 30vw;
        object-fit:cover;    
    } 
}
@media screen and (min-width:1000px) {
    .card{
        height: 35vw;
    }
    .card:hover{
        width: 85%;
        height: 38vw;
    }
    .card>h2{
        font-size: 2.3vw;
        padding: 4%;
    }
    .card>a>img{
        margin: 9px 0;
        width: 81%;
        height: 20vw;
        object-fit:cover;    
    } 
}

@media screen and (min-width:1250px) {
    .card{
        height: 25vw;
    }
    .card:hover{
        width: 85%;
        height: 32vw;
    }
    .card>h2{
        font-size: 2vw;
        padding: 4%;
    }
    .card>a>img{
        margin: 9px 0;
        width: 81%;
        height: 15vw;
        object-fit:cover;    
    } 
}
.contant-cards{
    margin: 14vw 0 0;
    background-color:#fff;
    padding: 25px 0;
    text-align: center;

}

.contant-cards>h1{
    font-size: 4vw;
    padding: 20px 0;
}
.cards{
    width: 100% ;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    padding: 10PX 0;
}
@media screen and (min-width:500px) {
    .contant-cards{
        margin: 10vw 0 0;

    }

}
@media screen and (min-width:750px) {
    .contant-cards{
        margin: 8vw 0 0;

    }

}

:root{
    --color-home:#0061a9;
    --color-about:#fff;
}
.footer{ 
    width: 100%; 
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color:#0061a9;
    background-color:var(--color-home) ;
}

.footer>hr{
    width: 90%;
    padding: 0.1px;
    margin: 1% 0;
    border: none;
    background-color: black;
}
.footer>p{
    font-family: 'Times New Roman', Times, serif;
    font-size: 2.5vw;
}
@media screen and (min-width: 768px) {
    .footer>p{
        font-size: 2.2vw;      
    }
}
@media screen and (min-width: 1024px) {
.footer{
    width: 100%;
}
.footer>p{
    font-size: 1.5vw;
}
}

.header{
    width: 100%;
    height: 18vw;
    display: flex;
    position: fixed;
    top: 0;
    padding: 0 10%;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(21, 255, 255);
    box-shadow: 0 4px 25px -9px black;
    z-index: 99;

}

.header>i{
    color: #ffff;
}
.header>img{
    width: 15%;
}

.search{
    width: 100%;
    height: 15vw;
    position: absolute;
    top: -100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #9fd5d1;
    z-index: 98;
    overflow-x: hidden;
    transition: all 1s ease-in-out;
}
.search.active{
    top: 18vw;
    position: fixed;
}
.search>input{
    width: 80%;
    height: 6vw;
    border: none;
    border-radius: 15px; 
    text-align: center;
}

@media screen and (min-width:320px) {
     .header{
         height: 15vw;
     }
     .search.active{
        top: 15vw;
        position: fixed;
    }
}

@media screen and (min-width:500px) {
    .search{
        height: 10vw;
    }
    .search>input{
    height: 5vw;
    }
    .search.active{
        top: 10vw;
        position: fixed;
    }
    .header{
        height: 10vw;
    }
    .header>img{
        width: 10%;
    }
    .header>i{
        font-size: 3vw;
    }
}
@media screen and (min-width:750px) {
    .header{
        height: 8vw;
    }
    .header>img{
        width: 8%;
    }
    .header>i{
        font-size: 2.5vw;
    }
    .search{
        height: 8vw;
    }
    .search>input{
    height: 4vw;
    }
    .search.active{
        top: 8vw;
        position: fixed;
    }
}
